.paginationjs .paginationjs-pages li.paginationjs-next > a,
.paginationjs .paginationjs-pages li.paginationjs-prev > a {
  background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%) !important;
  border-radius: 50%;
  color: #fff;
}

.paginationjs-prev,
.paginationjs-next {
  position: relative;
}

.paginationjs-prev::before {
  content: '';
  position: absolute;
  height: 2px;
  width: 14px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
}

.paginationjs-prev::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 45%;
  transform: translate(-50%, -50%) rotate(-45deg);
  border: solid #ffffff;
  border-width: 2px 0 0 2px;
  padding: 4px;
}

.paginationjs-next::before {
  content: '';
  position: absolute;
  height: 2px;
  width: 14px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
}

.paginationjs-next::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 55%;
  transform: translate(-50%, -50%) rotate(135deg);
  border: solid #ffffff;
  border-width: 2px 0 0 2px;
  padding: 4px;
}

.paginationjs .paginationjs-go-input > input[type='text'] {
  line-height: 28px\9;
  vertical-align: middle\9;
}
.paginationjs.paginationjs-big .paginationjs-pages li > a {
  line-height: 36px\9;
}
.paginationjs.paginationjs-big .paginationjs-go-input > input[type='text'] {
  height: 36px\9;
  line-height: 36px\9;
}

