@import '../../styles/global';

.booking {
  width: 380px;
  height: 512px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 45px 30px 30px 30px;
  &__id {
    & .numberRoomAndPrice__id {
      font-size: 23px;
      margin-right: 7px;
    }
    & .numberRoomAndPrice__price {
      transform: translate(-3px, 1px);
    }
    & .numberRoomAndPrice__period {
      transform: translate(0px, 1px);
    }
  }
  &__dateDropdown {
    margin-top: 5px;
  }
  &__guestDropdown {
    margin-top: 17px;
  }
  &__list {
    margin-top: 19px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 11px;
  }
  &__priceToDays {
    display: flex;
    & span {
      margin-right: 5px;
      width: 214px;
      line-height: 18px;
    }
  }
  &__priceInfo {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid $dark25;
    transform: translateY(-1px);
  }
  &__priceSymbol {
    color: $dark25;
    font-weight: 700;
    font-size: 12px;
    text-align: center;
    transform: translateY(-2px);
  }
  &__totalPrice {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__totalPrice-text {
    font-weight: 700;
    font-size: 19px;
    line-height: 23px;
    color: $dark100;
  }
  &__totalPrice-underline {
    border-bottom: 1px dotted $dark25;
    height: 23px;
    max-width: 180px;
    width: 100%;
    transform: translateY(-5px);
  }
  &__button {
    margin-top: 20px;
    transform: translateX(-2px);
  }
}
